import uniRate from '@/components/uni-rate/uni-rate.vue'
export default {
name: "",
components: {
uniRate
},
props: {},
data () {
  return {
	 trailer:[],
	 like:[]
    }
  },
  methods: {
	  getTrailer(){
	  		uni.request({
	  			url:'https://www.imovietrailer.com/superhero/index/movie/hot?type=trailer&qq=2684425594',
	  			method:'POST',
	  			success: (res) => {
	  				this.trailer=res.data.data
	  			
	  			}
	  		})
	  	},
	getLike(){
		uni.request({
			url:'https://www.imovietrailer.com/superhero/index/guessULike?qq=2684425594',
			method:'POST',
			success: (res) => {
				this.like=res.data.data
				console.log(this.like);
			}
		})
	},
	goDetails(e){
		uni.navigateTo({
			url:`../details/details?id=${e}`
		})
	}     
  },
  mounted () {
   this.getTrailer()
  this.getLike()
  },
  onLoad () {

  },
  filters: {

  },
  computed: {

  },
  watch: {

  },
  directives: {

  }
}
